import { useQuery } from 'react-query';
import styled from 'styled-components';
import {
  getLatestMovies,
  getNowPlayMovies,
  getPopularMovies,
  getTopRatedMovies,
  getUpcomingMovies,
  IGetMoivesResult,
  ILatest,
} from '../api';
import MovieModal from '../Components/Modal';
import MovieSlider from '../Components/Slider';
import { useMatch } from 'react-router-dom';
import Loader from '../Components/Loader';
import Banner from '../Components/Banner';
import Header from '../Components/Header';
import { useEffect, useState } from 'react';

const Wrapper = styled.div`
  background-color: black;
  padding-bottom: 300px;
  overflow-x: hidden;
`;

const Container = styled.div`
  position: relative;
  top: -100px;
  font-size: 1rem;
`;

function Movie() {
  // const navigate = useNavigate();
  const bigMatchMovie = useMatch('/movies/:Id'); //  url   url   url ,    null 
  const matchMovieId = String(bigMatchMovie?.params.Id); // useMatch  url  url  id 
  const { data: nowPlaying, isLoading: nowPlayingLoading } =
    useQuery<IGetMoivesResult>(['movies', 'nowPlaying'], getNowPlayMovies);
  const { data: upComing, isLoading: upComingLoading } =
    useQuery<IGetMoivesResult>(['movies', 'upComing'], getUpcomingMovies);
  const { data: popular, isLoading: popularLoading } =
    useQuery<IGetMoivesResult>(['movies', 'popular'], getPopularMovies);
  const { data: topRate, isLoading: topRateLoading } =
    useQuery<IGetMoivesResult>(['movies', 'topRate'], getTopRatedMovies);
  const { data: latest, isLoading: latestLoading } = useQuery<ILatest>(
    ['movies', 'latest'],
    getLatestMovies
  );

  const [windowSize, setWindowSize] = useState(window.innerWidth);

  useEffect(() => {
    window.addEventListener('resize', () => setWindowSize(window.innerWidth));
  }, [windowSize]);
  // const windowSize = window.innerWidth + '';
  // console.log(windowSize);

  // const scrollData = document.body.style.top;
  // const scrollPosition = +scrollData.replace(/[^0-9]/g, '') + 50;

  const loading =
    nowPlayingLoading ||
    upComingLoading ||
    popularLoading ||
    topRateLoading ||
    latestLoading;
  return (
    <Wrapper>
      <Header />
      {loading ? (
        <Loader />
      ) : (
        <>
          <Banner movieApi={nowPlaying} mediaType="movie" />
          <Container>
            {nowPlaying ? (
              <MovieSlider
                key="nowkey"
                movieApi={nowPlaying}
                title="   "
                mediaType="movie"
                windowSize={windowSize}
              />
            ) : null}

            {upComing ? (
              <MovieSlider
                key="upComkey"
                movieApi={upComing}
                title="  "
                mediaType="movie"
                windowSize={windowSize}
              />
            ) : null}
            {popular ? (
              <MovieSlider
                key="popkey"
                movieApi={popular}
                title="  "
                mediaType="movie"
                windowSize={windowSize}
              />
            ) : null}
            {topRate ? (
              <MovieSlider
                key="topkey"
                movieApi={topRate}
                title="  "
                mediaType="movie"
                windowSize={windowSize}
              />
            ) : null}
          </Container>
        </>
      )}
      <MovieModal
        matchId={matchMovieId}
        mediaType={'movie'}
        where={'movies'}
        // scrollPosition={scrollPosition}
      />
    </Wrapper>
  );
}

export default Movie;
