import { useQuery } from 'react-query';
import styled from 'styled-components';
import {
  getAiringTodayTV,
  getLatestTV,
  getOnTheAirTV,
  getPopularTV,
  getTopRatedTV,
  IGetMoivesResult,
  ILatest,
} from '../api';
import MovieModal from '../Components/Modal';
import MovieSlider from '../Components/Slider';
import { useMatch } from 'react-router-dom';
import Loader from '../Components/Loader';
import Banner from '../Components/Banner';
import Header from '../Components/Header';

const Wrapper = styled.div`
  background-color: black;
  padding-bottom: 300px;
  overflow-x: hidden;
`;

const Container = styled.div`
  position: relative;
  top: -100px;
`;

function Tv() {
  const bigMatchTv = useMatch('/tv/:Id'); //  url   url   url ,    null 
  const matchTvId = bigMatchTv?.params.Id + ''; // useMatch  url  url  id 
  // console.log('bigMatchTv', bigMatchTv);
  // console.log('matchTvId', matchTvId);
  const { data: onTheAir, isLoading: onTheAirLoading } =
    useQuery<IGetMoivesResult>(['Tv', 'nowPlaying'], getOnTheAirTV);
  const { data: popular, isLoading: popularLoading } =
    useQuery<IGetMoivesResult>(['Tv', 'popular'], getPopularTV);
  const { data: airing, isLoading: airingLoading } = useQuery<IGetMoivesResult>(
    ['Tv', 'upComing'],
    getAiringTodayTV
  );
  const { data: topRate, isLoading: topRateLoading } =
    useQuery<IGetMoivesResult>(['Tv', 'topRate'], getTopRatedTV);
  const { data: latest, isLoading: latestLoading } = useQuery<ILatest>(
    ['Tv', 'latest'],
    getLatestTV
  );

  // const scrollData = document.body.style.top;
  // const scrollPosition = +scrollData.replace(/[^0-9]/g, '');
  const loading =
    onTheAirLoading ||
    airingLoading ||
    popularLoading ||
    topRateLoading ||
    latestLoading;
  return (
    <Wrapper>
      <Header />
      {loading ? (
        <Loader />
      ) : (
        <>
          <Banner movieApi={onTheAir} mediaType="tv" />

          <Container>
            {onTheAir ? (
              <MovieSlider
                key="airkey"
                movieApi={onTheAir}
                title="   TV"
                mediaType="tv"
              />
            ) : null}
            {popular ? (
              <MovieSlider
                key="popTkey"
                movieApi={popular}
                title="  TV"
                mediaType="tv"
              />
            ) : null}
            {airing ? (
              <MovieSlider
                key="airingkey"
                movieApi={airing}
                title="  TV"
                mediaType="tv"
              />
            ) : null}
            {topRate ? (
              <MovieSlider
                key="topTkey"
                movieApi={topRate}
                title="  TV"
                mediaType="tv"
              />
            ) : null}
          </Container>
          <MovieModal
            matchId={matchTvId}
            mediaType={'tv'}
            where={'tv'}
            // scrollPosition={scrollPosition}
          />
        </>
      )}
    </Wrapper>
  );
}

export default Tv;
